import React from 'react'
import {draw, setup, useDeviceDetect} from '../../../../Helpers'
import { ImageWrapper, ProjectMainSection } from '../../../SectionsComponents'
import dynamic from 'next/dynamic'

const Sketch = dynamic(() => import('react-p5'), { ssr: false })
/*IMAGES*/
const MainStatue = '/images/Manticore/MainStatue.png'

const opt = {
  statueW: 610,
  statueH: 840,
  statue: MainStatue,
  posX: 'right',
  posY: 'bottom',
  proportions: 'origin',
  offsetX: 0,
  offsetY: 0,
  bg: '#fff'
}

const optMobile = {
  statueW: '350px',
  statueH: 'auto',
  statue: MainStatue,
  posX: 'right',
  posY: 'bottom',
  proportions: 'origin',
  offsetX: 0,
  offsetY: 0,
  bg: '#fff'
}


const MainSectionFooterContent = () => {
  const { minWidthLaptop } = useDeviceDetect()

  // return minWidthLaptop ? (
  //   <Sketch setup={setup(opt)} draw={draw} className="section-image-wrap main-statue"/>
  // ) : (
  //     <Sketch setup={setup(opt)} draw={draw} className="section-image-wrap main-statue"/>
  // )

  return (
      <Sketch setup={minWidthLaptop ? setup(opt) : setup(optMobile)} draw={draw} className="section-image-wrap main-statue"/>
  )
}

export const Main = () => {
  return (
    <ProjectMainSection id="manticore" name="Manticore" content={MainSectionFooterContent} isContentWhite/>
  )
}
